import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import bridge from "@vkontakte/vk-bridge";
import { Panel, Group, Header, SimpleCell, RichCell, InfoRow, Avatar, Accordion, Div } from '@vkontakte/vkui';
import { lastStudentsScheduleUpdate, lastTeachersScheduleUpdate } from '../utils/Database';

const Information = ({ id }) => {
    const infoStyle = { color: 'var(--vkui--color_text_subhead)' };
    const [author, setAuthor] = useState({});

    useEffect(() => {
        async function getData() {
			bridge
				.send('VKWebAppGetUserInfo', { user_id: 480989368 })
				.then((data) => {
					if (data.id) {
						setAuthor({url: "https://vk.com/id" + data.id, avatar: data.photo_100, first_name: data.first_name, last_name: data.last_name});
					}
				});
		}
        getData();
    }, []);

    return (
        <Panel id={id}>
            <Group header={<Header mode="secondary">Техническая информация</Header>}>
                <SimpleCell multiline>
                    <InfoRow header="Версия приложения">1.2.1</InfoRow>
                </SimpleCell>
                <SimpleCell multiline>
                    <InfoRow header="Последнее обновление расписания студентов">{lastStudentsScheduleUpdate()}</InfoRow>
                </SimpleCell>
                <SimpleCell multiline>
                    <InfoRow header="Последнее обновление расписания преподавателей">{lastTeachersScheduleUpdate()}</InfoRow>
                </SimpleCell>
            </Group>
            <Group header={<Header mode="secondary">Общая информация</Header>}>
                <Accordion>
                    <Accordion.Summary multiline iconPosition="before">Как часто обновляется расписание?</Accordion.Summary>
                        <Accordion.Content>
                            <Div style={infoStyle}>
                                Расписание обновляется из официального сайта БГТУ при наличии изменений, начале нового семестра или сессии.
                            </Div>
                        </Accordion.Content>
                </Accordion>
            </Group>
            {Object.keys(author).length > 0 && (
                <Group header={<Header mode="secondary">Контакты</Header>}>
                    <RichCell multiline
                        onClick={() => window.open(author.url, '_blank').focus()}
                        before={<Avatar size={48} src={author.avatar}/>}
                        text="Разработчик приложения"
                        caption="Обращаться по любым вопросам"
                    >{author.first_name} {author.last_name}</RichCell>
                </Group>
            )}
        </Panel>
    );
};

Information.propTypes = {
    id: PropTypes.string.isRequired,
};

export default Information;